import { Component, OnInit } from '@angular/core';
import { ServiceCartService } from 'src/app/service-cart.service';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.page.html',
  styleUrls: ['./product-details.page.scss'],
})
export class ProductDetailsPage implements OnInit {

  constructor(private cartService: ServiceCartService) { }

  updatedProductArr = [];
  returnCart = [];

  returnDetailsCart = []
  
  ngOnInit() {
    this.returnCart = this.cartService.getCart(); // Get Added Product
    this.updatedProductArr = this.cartService.getProducts();
  }

}
